// BODY

@import url('https://fonts.googleapis.com/css?family=Roboto+Condensed&display=swap');


body {
  font-family: 'Roboto Condensed', sans-serif;
}


// NAVBAR  /////
.header-main {

      border-bottom: 1px solid #dedede;
      -webkit-box-shadow: 0px 2px 3px 0px rgba(0,0,0,0.38);
      -moz-box-shadow: 0px 2px 3px 0px rgba(0,0,0,0.38);
      box-shadow: 0px 2px 3px 0px rgba(0,0,0,0.38);
      min-height: 50px;
      background-color: rgb(255, 255, 255);

  .container {

    .navbar {
      padding-top: 0;
      padding-bottom: 0;

      .navbar-header {
        margin-top: 0;
      }

      .active {
        background: #00609f;
        color: #ffffff;
        border-top: 3px solid #00609f !important;
      }

      .nav-pills {
        .nav-link {
          padding-top: 30px;
          padding-bottom: 30px;
          border-radius: 0;
          padding-left: 25px;
          padding-right: 25px;
          border-top: 3px solid #fff;
          &:hover {
           border-top: 3px solid #00609f;
          }
        }
      }
      width: 100%;
     
      li {
         // padding: 15px;
         padding: 0;
         text-align: center;
        
        
      }
    
      a {
        color: #000;
        font-size: 18px;
        font-weight: 800;
        text-transform: uppercase;
      }
      #brand {
        margin-left: 40px;
        width: 60px;
        float: left;
        &:hover {
          border: none;
        }
      }
    
      img {
        width: 80px;
        height: 100%;
        // margin-bottom: 10px;
        top: 0;
      }
    
      .navbar-toggle{
        border: none;
    
      .icon-bar {
        background-color:#000;
          }
        }
    }
  }
 
}




// Slider //////

.slider {

  .carousel-item{
    height: 650px;
} 
.carousel-item img{
    height: 650px;
}

  .owl-prev {
    left: 30px;
    position: absolute;
    top: 50%;
    margin: -20px 0 0 0;
    padding: 0;
    width: 40px;
    height: 40px;
    border-radius: 0;
    font-size: 30px;
    line-height: 30px;
    border: solid 3px #ffffff;
    // background: transparent;
    color: #ffffff;
    transform: rotate(45deg);

    .fa-angle-left, .fa-angle-right {
      margin-top: 3px;
      margin-right: 3px;
      transform: rotate(-45deg);
    }

    &:hover {
      background-color: #00609f ;
    }
  }

  .owl-next {
    right: 30px;
    position: absolute;
    top: 50%;
    margin: -20px 0 0 0;
    padding: 0;
    width: 40px;
    height: 40px;
    border-radius: 0;
    font-size: 30px;
    line-height: 30px;
    border: solid 3px #ffffff;
    // background: transparent;
    color: #ffffff;
    transform: rotate(45deg);

   .fa-angle-right {
      margin-bottom: 3px;
      margin-left: 3px;
      transform: rotate(-45deg);
    }

    &:hover {
      background-color: #00609f ;
    }
  }

  .carousel-caption {

    position: absolute;
    top: 7%;
     
    .background-left {
      // background-color: rgba(0,0,0,0.5);
      padding: 10px;
      width: 60%;
      // margin: 0 auto 0 auto;
      // border: 2px solid #ffffff;
      
    h2 {
      font-weight: 800;
      font-size: 65px;
      line-height: 65px;
    }

      p {
        font-size: 18px;
        line-height: 20px;
        color: #ffffff;
        margin-bottom: 0;
      }

      .btn {
        border-radius: 0;
        padding: 10px;

        &:hover {
          background-color: #fff;
          color: #00609f;
        }
      }

      .btn-theme {
        margin-top: 40px;
        border: 1px solid #ffffff;
        background-color: transparent;
        color: #ffffff

        
      }
    }

  
  }

  .carousel-indicators {
    li {
      height: 8px;
    }
  
    .active {
      background-color: #00609f;
    }
  }

}

// Ukratko O NAMA /////////

.ukratko-o-nama {
  margin-top: 50px;
  margin-bottom: 50px;
  padding-top: 70px;
  padding-bottom: 70px;
  color: #fff;
  background-color: #00609f;

  .text-left {
    text-align: left;
  }

  .section-title {
    margin-top: 0;
    margin-bottom: 25px;
    font-size: 30px;
    font-weight: 900;
    line-height: 30px;
    text-transform: uppercase;
  }

  p {
    margin-bottom: 20px;
}

ul {
  padding-left: 0;
  list-style: none;
  margin-bottom: 20px;

    li {
      // padding-left: 30px;
      // position: relative;

      i {
        padding-right: 20px;

        .fa {
          position: absolute;
          left: 0;
          top: 3px;
        }
      }

    
  }
}

.owl-prev,
.owl-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.owl-prev {
  left: 0;
}

.owl-next {
  right: 0;
}

.btn-row {

  .btn {
    margin-top: 10px;
    margin-right: 10px;
    border-radius: 0;
  }

  .btn-theme {
    color: #ffffff;
    border-width: 1px;
    background-color: #00609f;
    border-color: #fff;
    padding: 15px 20px;
    font-family: 'Raleway', sans-serif;
    font-size: 14px;
    font-weight: 600;
    line-height: 1;
    text-transform: uppercase;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
}

.btn-theme-1 {
  &:hover {
    background-color: #fff;
    color: #000;
  }
}

.btn-theme-transparent {
  &:hover {
    background-color: #000;
    color: #ffffff;
  }
}
  
}
}

// Extra Ponuda ////////

.extra-ponuda{
  margin-top: 70px;
  margin-bottom: 50px;
  position: relative;

  .section-title {
    margin-top: 0;
    margin-bottom: 25px;
    font-size: 30px;
    font-weight: 900;
    line-height: 30px;
    text-transform: uppercase;
    text-align: center;
    color: #14181c;
    overflow: hidden;

    small {
      display: block;
      font-size: 18px;
      font-weight: 400;
      line-height: 18px;
      text-transform: none;
      margin-bottom: 10px;
  }

   
  } 

  // .swiper {
    
  .swiper-button-prev, .swiper-button-next {
    position: absolute;
    top: 50%;
    width: 40px;
    height: 40px;
    line-height: 40px;
    margin-top: -20px;
    text-align: center;
    background: none;
    border: solid 4px #14181c;
    // -webkit-transform: rotate(45deg);
    // -ms-transform: rotate(45deg);
    // -o-transform: rotate(45deg);
    transform: rotate(45deg);
    z-index: 999;
    cursor: pointer;
}

  .swiper-button-prev {
    left: 120px;
    .fa-angle-left {
      font-size: 25px;
    font-weight: 900;
    -webkit-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    -o-transform: rotate(-45deg);
    transform: rotate(-45deg);
    color: #000;
    }

    &:after {
      display: none;
    }
  }
  .swiper-button-next {
    right: 120px;

    .fa-angle-right {
      font-size: 25px;
    font-weight: 900;
    -webkit-transform: rotate(-45deg);
    -ms-transform: rotate(-45deg);
    -o-transform: rotate(-45deg);
    transform: rotate(-45deg);
    color: #000;
    }

    &:after {
      display: none;
    }
  }


  .no-border {
    border: solid 1px #e9e9e9;
  }

  .no-padding {
    padding: 0;
  }

  .caption-title {
    font-size: 18px;
    font-weight: 700;
    a {
      text-decoration: none;
      color: #000;
      transition: color .5s ease;
      &:hover {
        color: #00609f;
      }
    }
}

.caption-text {
  margin-bottom: 10px;
  color: #7f7f7f;
}

.buttons {
  margin-bottom: 15px;
  .btn-theme {
    color: #ffffff;
    border-width: 1px;
    background-color: #00609f;
    border-color: #fff;
    border-radius: 0;
    padding: 10px 20px;
  }
}

.table {
  margin-bottom: 0;
}

.icon-view {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -40px;
  margin-top: -40px;
  opacity: 0;
  -webkit-animation: fadeIn .4s;
  animation: fadeIn .4s;
  width: 80px;
    height: 80px;
    padding: 5px;
    border: solid 1px rgba(0, 0, 0, 0.6);
    border-radius: 50%;
    display: inline-block;

 

  strong {
    display: block;
    width: 68px;
    height: 68px;
    border-radius: 50%;
    text-align: center;
    background-color: rgba(0, 0, 0, 0.8);
    color: #ffffff;

    .fa-eye {
      position: relative;
      top: 22px;
      transition: all .5s ease-in-out;
    }
}
}

.media{
  position: relative;
  &:hover {
    .icon-view {
      opacity: 1;
      transition: opacity .4s ease-in-out;
    }
  }
}

}


// Footer ////////////

.page-footer {
  background-color: #00609f;
  color: #ffffff;

  .mapa {
    p {
    line-height: 10px;
    a {
      text-decoration: none;
      color: #fff;
      margin-left: 10px;
    }
    }
  }


  hr {
    background-color: #fff;
  }

  .copy {
    text-decoration: none;
  }

  .btn-sm {
   
    display: inline-block;
    position: relative;
    cursor: pointer;
    width: 37px;
    height: 37px;
    border:2px solid #ddd; /* add border to the buttons */
    box-shadow: 0 3px 3px #999;
    padding: 0px;
    text-decoration: none;
    text-align: center;
    color: #fff;
    font-size: 16px;
    font-weight: normal;
    line-height: 2em;
    border-radius: 27px;
    -moz-border-radius:27px;
    -webkit-border-radius:27px;

    &:hover {
      color: #00609f;
      background-color: #fff;
    }
  }
}

.kontakt-section {
  margin: 50px 0 50px 0;

  h3 {
    text-transform: uppercase;
    text-shadow: 1px 1px 1px rgba(0,0,0,0.7);
    margin: 0;
  }
  em {
    padding-bottom: 20px!important;
    font-weight: 500;
  }

  .obavezno {
    color: #00609f;
  }

  .formStyle {
    margin-top: 25px;
  }

  .form-control {
    border-radius: 0px;
    border: none;
    border-bottom: 1px solid black;

    &:focus {
      // border: 2px solid rgb(255, 0, 0);
      border-bottom-color: #f15a29;
      transition: border-color 0.6s ease-in-out;
      box-shadow: 0px 0px #f15a29;

    }
  }

  .control-pitanje {
    border: 1px solid black;

    &:focus {
      // border: 2px solid rgb(255, 0, 0);
      border-color: #f15a29;
      transition: border-color 0.6s ease-in-out;
      box-shadow: 0px 0px #f15a29;

    }
  }

  label {
    margin-top: 30px;
    margin-bottom: 0;
  }

  .mojedugme {
    background-color: #f15a29;
    color: #fff;
    margin-top: 25px;
    padding-right: 18px;

    &:hover {
      background-color: #fff;
      color: #f15a29;
      border: 1px solid #f15a29;
      transition: all .9s ease;
    }
  }

  .details {
    margin-top: 80px;
    padding-left: 40px;
    p {
      font-size: 15px;
      color: #888787;
        .fa-2x {
          color: #f15a29!important;
      }
     
    }
  }
  
}